/* 
    M O B I L E
*/
@media screen and (max-width: 1200px) {
  html,
  body {
    min-width: 320px;
    width: 100%;
    overflow-x: hidden; }
  a:focus {
    outline: none; } }

@media screen and (max-width: 991px) {
  #hamburger-icon {
    display: block; }
